<template>
  <div class='root'>
    <action-bar title='编辑预售商品'></action-bar>
    <div class='container'>
      <scroll-view class='content' :data="goodsList">
        <div class="cc">
          <div class="content-box">
            <div class="flex-center margin-bottom">
              <span>标题：</span>
              <div class="title">{{ data.title }}</div>
            </div>
            <div class="flex-center margin-bottom">
              <span>类型：</span>
              <div>固定模式</div>
            </div>
            <div class="margin-bottom">
              <div class="margin-bottom">活动时间：</div>
              <div class="flex-center">
                {{data.startSalesTime}}
                <span class="to">至</span>
                {{data.endSalseTime}}
              </div>
            </div>
            <div class="margin-bottom">
              <div class="margin-bottom">预计送达时间段：</div>
              <div class="flex-center">
                {{data.startDeliveryDate}}
                <span class="to">至</span>
                {{data.endDeliveryDate}}
              </div>
            </div>
          </div>
          <div class="content-box">
            <div class="flex-center goods-add margin-bottom">
              <div class="goods-title">添加商品</div>
              <div class="goods-quantity">共{{ goodsList.length }}件</div>
            </div>
            <div class="add-goods-box">
              <div class="no-font-size" @click="bindAddGoods">
                <img class="add-goods-img" src="@/assets/home_add_commoidy_icon.png" />
              </div>
              <div class="hor-goods-box">
                <div class="hor-scroll">
                  <van-image v-for="item in goodsList" :key="item.id" class="hor-goods-img" :src="baseUrl + item.c" />
                </div>
              </div>
            </div>
          </div>
          <goods-item v-for="(item, index) in goodsList" :key="item.id" class="content-box">
            <van-image slot="imgSlot" radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
            <div slot="nameSlot">
              <img class="delete-img" src="@/assets/icon_chongzhi.png" @click="bindDeleteGoods(index)" />
              <div>{{ item.productName }}</div>
            </div>
            <div slot="attributeSlot">
              <div class="attribute">
                <div class="attribute-content">分类：{{ item.categoryName }}</div>
                <div class="attribute-content">颜色：{{ item.color }}</div>
              </div>
              <div class="attribute">
                <div class="attribute-content">等级：{{ item.level }}</div>
                <div class="attribute-content">规格：{{ item.unit }}</div>
              </div>
            </div>
            <div slot="menuSlot" class="goods-bottom">
              <div class="price-stock">
                <span>价格(元)</span><input maxlength="10" type="digit" placeholder="0.00元" v-model="item.priceStay"
                  class="input-bg"  />
              </div>
              <div class="price-stock">
                <span>库存(扎)</span><input type="number" maxlength="10" placeholder="0扎" v-model="item.stockStay"
                  class="input-bg"  />
              </div>
            </div>
          </goods-item>
        </div>
      </scroll-view>
    </div>
    <div class="bottom">
      <div class="agree">
      </div>
      <div @click="bindSubmit" class="release release-true">发布</div>
    </div>
  </div>
</template>
<script>

import { modifyPresellActivity, presellActivityInfo } from '@/http/api'
import goodsItem from '@/components/util/GoodsItem.vue'

export default {
  name: 'editReserveShop',
  components: {
    goodsItem
  },
  data () {
    return {
      id: '',
      data: {},
      goodsList: [] // 商品数据
    }
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  methods: {
    bindDeleteGoods (index) {
      const goodsList = this.goodsList
      if (index < goodsList.length) {
        goodsList.splice(index, 1)
        this.goodsList = goodsList
      }
    },
    bindSubmit () {
      const goodsList = this.goodsList
      if (goodsList.length === 0) {
        this.$toast('请添加预售商品')
        return
      }
      const item = []
      for (const goods of goodsList) {
        if (!goods.priceStay) {
          this.$toast('<' + goods.productName + '>价格未填写')
          return
        }
        if (!goods.stockStay) {
          this.$toast('<' + goods.productName + '>库存未填写')
          return
        }
        item.push({
          price: goods.priceStay,
          inventory: goods.stockStay,
          productId: goods.productId
        })
      }
      const data = {
        id: this.id,
        data: JSON.stringify(item)
      }
      modifyPresellActivity(data).then(res => {
        this.$router.back()
        this.$toast.success('修改成功')
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    bindAddGoods () {
      this.$router.push('/spotGoodsList')
    },
    getReserveDetail () {
      presellActivityInfo({
        id: this.id
      }).then(res => {
        const goodsList = res.data.productList
        goodsList.forEach(elt => {
          elt.priceStay = elt.earnest
          elt.stockStay = elt.inventory
        })
        this.data = res.data
        this.goodsList = goodsList
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.id = this.$route.query.id
    this.getReserveDetail()
  },
  beforeRouteEnter (to, from, next) {
    next()
    const list = from.params.list
    if (list) {
      next(vm => {
        vm.goodsList = list
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
  position: relative;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #F2F2F2;
}

.cc {
  padding-top: .5rem;
  padding-bottom: .0031rem;
}

.title {
  flex-grow: 1;
  color: #42D18A;
}

.flex-center {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.margin-bottom {
  margin-bottom: .75rem;
}

.date {
  flex-grow: 1;
}

.content-box {
  margin: 0 .5rem;
  background-color: white;
  border-radius: .25rem;
  padding: .5rem;
  font-size: .875rem;
  margin-bottom: .5rem;
}

.input-date {
  background-color: #f5f7f9;
  border-radius: .25rem;
  padding: .625rem;
  border: none;
  text-align: center;
  width: 86%;
}

.to {
  margin: 0 .75rem;
}

.goods-add {
  justify-content: space-between;
}

.goods-title {
  font-size: 1.125rem;
  color: black;
}

.goods-quantity {
    font-size: .875rem;
    color: #999999;
}

.add-goods-box {
    display: flex;
    padding: .25rem 0;
}

.no-font-size {
    font-size: 0;
    margin-right: .5rem;
}

.add-goods-img {
    width: 2.625rem;
    height: 2.625rem;
    padding: .25rem;
    border: #333333 dashed .0625rem;
    border-radius: 2px;
    box-sizing: border-box;
}

.hor-goods-box {
    flex-grow: 1;
    position: relative;
}

.hor-scroll {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
}

.hor-scroll::-webkit-scrollbar {
    display: none;
}

.hor-goods-img {
    width: 2.75rem;
    height: 2.75rem;
    background-color: #F2F2F2;
    border-radius: .25rem;
    font-size: 0;
    margin: 0 .25rem;
}

.goods-img {
    width: 20vw;
    height: 20vw;
    background-color: #F2F2F2;
    margin-right: .5rem;
}

.delete-img {
    width: 1.125rem;
    height: 1.125rem;
    float: right;
    margin-left: .375rem;
}

.attribute {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
}

.attribute-content {
    width: 50%;
}

.goods-bottom {
    display: flex;
    flex-direction: row;
    padding-left: .875rem;
    padding-right: .875rem;
    padding-top: .875rem;
    align-items: center;
    padding-bottom: .25rem;
}

.price-stock {
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    width: 50%;
    white-space: nowrap;
    align-items: center;
}

.input-bg {
    background-color: #f5f7f9;
    border-radius: .25rem;
    padding-left: .375rem;
    padding-top: .375rem;
    padding-bottom: .375rem;
    border: none;
    width: 80%;
}

.bottom {
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.agree {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #666666;
    font-size: .875rem;
}

.release {
    font-size: 1.125rem;
    text-align: center;
    color: white;
    width: 6.25rem;
    height: 2.875rem;
    line-height: 2.875rem;
    font-weight: 500;
    border-radius: 6%;
    margin: 0 .375rem;
}

.release-true {
    background-color: #42D18A;
}
</style>
